






















import Vue from 'vue'
import axios from 'axios'
import Component from 'vue-class-component'
import { i18n } from '@/plugins/i18n'
import { namespace } from 'vuex-class'

import pdf from 'vue-pdf'
import App from '@/App.vue'

const AppStore = namespace('AppStore')

@Component({
  components: {
    pdf,
  },
})
export default class Home extends Vue {
  @AppStore.Mutation setBooksReadedPage!: (page: number) => void
  @AppStore.State japaneseLearn!: number

  bookName = 'Японский_язык_Самоучитель_by_Лаврентьев_Б_П_z_lib_org.pdf'

  pdfKey = 0
  zoomed = false
  pageCount = 0

  zoomPdf() {
    this.zoomed = !this.zoomed
    this.pdfKey++
  }

  nextPage() {
    if (this.japaneseLearn >= this.pageCount) return
    this.setBooksReadedPage(this.japaneseLearn + 1)
    window.scrollTo(0, 0)
  }

  previousPage() {
    if (this.currentPage <= 1) return
    this.setBooksReadedPage(this.currentPage - 1)
  }

  get progress() {
    return (this.currentPage / this.pageCount) * 100
  }

  get pdfClass() {
    return this.zoomed ? 'pdf-file-width' : ''
  }

  get currentPage() {
    return this.japaneseLearn
  }
}
